* {
  padding: 0px;
  margin: 0px;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.nav {
  background-color: #2a2a2b;
  padding: 20px;
  color: white;
  position: sticky;
  top: 0;
}

.card-container {
  display: flex;
  padding: 10px;
  gap: 10px;
  border-radius: 15px;
  margin: 10px;
}

.card-container > img {
  height: 130px;
  width: 100px;
  border-radius: 10px;
}

.text-content {
  display: flex;
  flex-direction: column;
  text-align: start;
  gap: 1px;
}

.active {
  padding: 2px;
  color: rgb(0, 0, 0);
  font-size: 10px;
  height: 10px;
  width: 10px;
  /* border-radius:5px; */
  background-color: rgb(120, 248, 0);
}
.locaIcon {
  height: 15px;
}
